import React from 'react';
import { Link } from 'react-router-dom';
// margin m, padding p, top t, bottom b, left s, right e, 위아래 y, 좌우 x 
// 글자크기 fs-1, 흰색 text-white 회색 text-muted, 파랑 text-primary 초록 text-success 하늘색 text-info 노랑색 text-warning 빨간색 text-danger
// font-weight fw-bold
// 텍스트 줄간격 lh-lg

import youtubejson from '../pages/youtube/youtube.json'
import meshjson from '../pages/mesh/mesh.json'
import fluentjson from '../pages/fluent/fluent.json'

function Header() {
	const style = {
		fontSize: "25px",
		fontWeight: "bold",
		color: "#ddffaa"
  }
  return (
    <>
       <nav className="navbar navbar-expand-md fixed-top navbar-dark bg-primary p-2 ps-4" >
			 	<i className='fab fa-accusoft mx-2 fs-2 text-info' />
				<a class="navbar-brand" href="#" style={style}>Ansys.</a>
				<button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
					<span class="navbar-toggler-icon"></span>
				</button>
				<div class="collapse navbar-collapse" id="navbarNav">
					<ul className="navbar-nav me-auto mb-2 mb-lg-0 fs-5" >
						<li><Link className="nav-link" to={"/"} >Home</Link></li>
						
						{/* https://www.w3schools.com/icons/fontawesome5_icons_computers.asp */}
						<li><i className='fas fa-flask mx-2 mt-2 fs-2 text-light' /></li>

						{/* youtube */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								Ansys Youtube
							</a>
							<ul className="dropdown-menu">
								{youtubejson.youtubeData.map((items) => (
									<li><Link className="dropdown-item" to={items.url}>{items.title}</Link></li>
								))}
							</ul>
						</li>

						{/* Design Modeler */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								Design Modeler
							</a>

						</li>

						{/* SpaceClaim */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								SpaceClaim
							</a>

						</li>

						{/* MESHING */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								Meshing
							</a>
							<ul className="dropdown-menu">
								{meshjson.meshData.map((items) => (
									<li><Link className="dropdown-item" to={items.url}>{items.title}</Link></li>
								))}
							</ul>
						</li>

						{/* Fluent */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								Fluent
							</a>
							<ul className="dropdown-menu">
								{fluentjson.fluentData.map((items) => (
									<li><Link className="dropdown-item" to={items.url}>{items.title}</Link></li>
								))}
							</ul>
						</li>

						{/* CFD Theory */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								CFD Theory
							</a>

						</li>


					</ul>
				</div>


			</nav>
    </>
  );
}
export default Header;
